import { Injectable } from '@angular/core';
import { of, Observable } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { environment } from '../../../environments/environment';
import { HandleErrorService } from 'src/app/_service/handle-error.service';

@Injectable({
  providedIn: 'root'
})
export class GetCompanyListService {

  getCompanyListServiceUrl = environment.contentServer + '/api/contentMgmt/getCompanyList';

  constructor(private httpClient: HttpClient, private handleErrorService: HandleErrorService) { }

  getCompanyListService(requestData): Observable<any> {

    const httpOptions: { headers; observe; } = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      observe: 'response'
    };

    return this.httpClient.post<any>(this.getCompanyListServiceUrl, JSON.stringify(requestData), httpOptions)
      .pipe(
        tap(res => {
          // console.log(res);
        }),
        catchError(this.handleErrorService.handleError('getCompanyListService', []))
      );
  }
}
